*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

*:focus {
  outline: none; }

body {
  font-family: 'Poiret One', cursive;
  -webkit-overflow-scrolling: touch;
  overflow-x: hidden;
  height: auto; }
  @media (min-width: 1400px) {
    body {
      max-width: 1400px;
      margin: 0 auto; } }

ul {
  list-style-type: none; }

a {
  text-decoration: none !important;
  outline: 0 !important; }

.headline {
  color: #fff;
  font-size: 6vw; }

.missed-date h2 {
  font-size: 2em; }

.missed-date h3 {
  color: #fff; }

.titles {
  margin-top: 1em;
  font-size: 2em; }

.date {
  color: #fff;
  font-family: 'Dancing Script', cursive;
  font-size: 1.5em; }

h1 {
  color: #FF6363;
  font-weight: bold; }

p {
  font-size: 1.5em;
  color: #484848; }

section {
  text-align: center;
  padding: .5em 0;
  overflow: auto; }
  section:nth-child(odd) {
    background: #F2F2F2; }
  section:nth-child(even) {
    background: #fff; }

img {
  width: 100%; }

.content {
  margin: 2em; }

.hidden {
  display: none !important; }

.display {
  display: block; }

#countdown {
  position: relative;
  width: 100%;
  margin: 0 auto;
  padding: 20px 0 0px 0;
  color: #fff;
  border: 1px solid #adafb2;
  border-width: 1px 0;
  display: flex;
  font-family: 'Arial Narrow', Arial, sans-serif; }
  #countdown li {
    margin: 0 -3px 0 0;
    padding: .5em;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    font-size: 72px;
    font-size: 4vw;
    text-align: center; }
    @media (max-width: 767px) {
      #countdown li {
        font-size: 6vw; } }
    #countdown li .label {
      color: #adafb2;
      font-size: 18px;
      font-size: 1.5vw;
      text-transform: uppercase;
      display: block; }
      @media (max-width: 767px) {
        #countdown li .label {
          font-size: 3vw; } }

.btn {
  position: relative;
  display: inline-block;
  overflow: visible;
  margin: 5px 0;
  padding: 1em 1.5em 1em;
  border: 0;
  box-shadow: none;
  text-transform: uppercase;
  text-shadow: none;
  font-weight: bold;
  font-size: 13px;
  line-height: 1;
  transition-property: all .1s ease-in 0s;
  -webkit-font-smoothing: antialiased;
  color: #fff;
  border-radius: 3px;
  cursor: pointer;
  background: #FF6363; }

.btn-table {
  width: 50%; }

.btn-map {
